
import { defineComponent } from "vue";

export default defineComponent({
  setup(props, { emit }) {
    const paginate = (page: number, condition: boolean) => {
      if (!condition) return;
      emit("update:page", page);
    };
    return { paginate };
  },
  name: "BasePagination",
  props: {
    pages: { type: Number, required: true },
    page: { type: Number, required: true },
  },
});
