
import { computed, defineComponent, reactive, ref, watch } from "vue";
import { useStore } from "@/store";
import formatNumber from "@/helpers/formatNumber";
import { OrderForm } from "@/types/basic/order/OrderPage.form";
import BaseFormText from "@/components/Base/BaseFormText.vue";
import BaseFormField from "@/components/Base/Wrappers/BaseFormField.vue";
import BaseInputRadio from "@/components/Base/Wrappers/BaseInputRadio.vue";
import useLoadAxios from "@/composables/useLoadAxios";
import { API_BASIC_URL } from "@/others/basic-url";
import { ApiOrder, ApiPayments } from "@/types/api-types";
import InputRadioWrapper from "@/components/Base/Wrappers/InputRadioWrapper.vue";
import { DeliveryType } from "@/types/basic/delivery-type";
import { OrderStatus } from "@/types/custom/status";
import axios from "axios";
import { OrderFormErrors } from "@/types/basic/order/OrderPage.form.errors";
import { MutationTypes } from "@/types/store-type";
import router from "@/router";
import OrderList from "@/components/Order/OrderList.vue";

export default defineComponent({
  setup() {
    const store = useStore();
    const delivery = ref(1);
    const payment = ref(1);
    const status: OrderStatus = {
      errorInfo: ref(null),
      isLoadingFailed: ref(false),
      isLoading: ref(false),
    };
    const formData = new OrderForm();
    const formDataErrors = reactive(new OrderFormErrors());
    const order = () => {
      status.isLoading.value = true;
      status.isLoadingFailed.value = false;
      status.errorInfo.value = null;
      Object.assign(formDataErrors, new OrderFormErrors());
      axios
        .post<ApiOrder>(
          "api/orders",
          {
            ...{
              ...formData,
              phone: formData.phone.replaceAll(/( )|\(|\)|-/g, ""),
            },
            deliveryTypeId: delivery.value,
            paymentTypeId: payment.value,
          },
          {
            baseURL: API_BASIC_URL,
            params: { userAccessKey: store.state.userAccessKey },
          }
        )
        .then(({ data }) => {
          store.commit(MutationTypes.RESET_CART, undefined);
          store.commit(MutationTypes.UPDATE_ORDER_INFO, data);
          router.push({ name: "orderInfo", params: { id: data.id } });
        })
        .catch(({ response }) => {
          const error = response.data.error;
          status.isLoadingFailed.value = true;
          status.errorInfo.value = error.message;
          if (response.status === 400) {
            Object.assign(formDataErrors, error.request || {});
          }
        })
        .then(() => {
          status.isLoading.value = false;
        });
    };
    const toPrice = (value: string) => {
      if (!Number(value)) return "бесплатно";
      else return `${formatNumber(Number(value) || 0)} ₽`;
    };
    const {
      loadStatus: statusDeliveries,
      data: deliveries,
      doLoadData: doLoadDeliveries,
    } = useLoadAxios<Array<DeliveryType & { prettyPrice: string }>>(
      () => "api/deliveries",
      () => ({
        baseURL: API_BASIC_URL,
      }),
      (value) =>
        value.map((e) => Object.assign(e, { prettyPrice: toPrice(e.price) }))
    );
    doLoadDeliveries();
    const deliveryTitle = computed(() => {
      if (deliveries.value) {
        const result = deliveries.value.find((e) => e.id === delivery.value);
        if (result) {
          return result.prettyPrice;
        }
      }
      return "бесплатно";
    });
    const {
      loadStatus: statusPayments,
      data: payments,
      doLoadData: doLoadPayments,
    } = useLoadAxios<ApiPayments>(
      () => "api/payments",
      () => ({
        baseURL: API_BASIC_URL,
        params: { deliveryTypeId: delivery.value },
      })
    );
    doLoadPayments();
    const deliveryCost = computed(() => {
      if (deliveries.value) {
        const element = deliveries.value.find((e) => e.id === delivery.value);
        if (element) {
          return Number(element.price);
        }
      }
      return 0;
    });
    const products = computed(() => store.getters.cartProductsData);
    const wholeCost = computed(() =>
      formatNumber(store.getters.wholeCost + deliveryCost.value)
    );
    watch(
      () => delivery.value,
      () => {
        doLoadPayments().then(() => {
          if (payments.value) {
            payment.value = payments.value[0].id;
          }
        });
      }
    );
    return {
      products,
      wholeCost,
      order,
      deliveryTitle,
      formData,
      status,
      formDataErrors,
      statusDeliveries,
      statusPayments,
      deliveries,
      delivery,
      payment,
      payments,
    };
  },
  components: {
    OrderList,
    BaseFormText,
    BaseFormField,
    BaseInputRadio,
    InputRadioWrapper,
  },
  name: "OrderPage",
});
