
import { computed, defineComponent } from "vue";
import { useStore } from "@/store";
import formatNumber from "@/helpers/formatNumber";
import CartItem from "@/components/Cart/CartItem.vue";

export default defineComponent({
  components: { CartItem },
  setup() {
    const store = useStore();
    const count = computed(() => store.getters.numberOfPositions);
    const products = computed(() =>
      store.getters.cartProductsData.map((e) =>
        Object.assign(e, { prettyPrice: formatNumber(e.price) })
      )
    );
    const currPrice = computed(() => formatNumber(store.getters.wholeCost));
    return { count, products, currPrice };
  },
  name: "CartPage",
});
