import { ref, UnwrapRef } from "vue";

export default function <T>(
  value: T,
  defaultValue: T,
  array: Array<() => void>
) {
  const element = ref(value);
  const toDefault = () => {
    element.value = defaultValue as UnwrapRef<T>;
  };
  array.push(toDefault);
  return { element, toDefault };
}
