import type { ActionTree, GetterTree, MutationTree } from "vuex";
import { createLogger, createStore } from "vuex";
import type {
  Actions,
  CustomStore,
  Getters,
  Mutations,
  State,
} from "@/types/store-type";
import { ActionTypes, MutationTypes } from "@/types/store-type";
import axios from "axios";
import {
  ApiDeleteCartProducts,
  ApiGetCartProducts,
  ApiOrder,
  ApiPostCartProducts,
  ApiPutCartProducts,
} from "@/types/api-types";
import { API_BASIC_URL } from "@/others/basic-url";
import { Names } from "@/others/names";
import { CartProductType } from "@/types/basic/cart-product-type";
import { CartStatus } from "@/types/custom/status";

const state: State = {
  cartProductsData: [],
  userAccessKey: null,
  cartOrderInfo: null,
  cartStatus: { isLoading: false, isLoadingFailed: false },
};

const mutations: MutationTree<State> & Mutations = {
  [MutationTypes.UPDATE_USER_ACCESS_KEY](state, accessKey) {
    state.userAccessKey = accessKey;
  },
  [MutationTypes.UPDATE_CART_PRODUCTS_DATA](state, items) {
    state.cartProductsData = items;
  },
  [MutationTypes.RESET_CART](state) {
    state.cartProductsData.length = 0;
  },
  [MutationTypes.UPDATE_ORDER_INFO](state, payload) {
    state.cartOrderInfo = payload;
  },
};

export const actions: ActionTree<State, State> & Actions = {
  [ActionTypes.LOAD_ORDER_INFO]({ commit, state }, { status, orderId }) {
    status.isLoadingFailed.value = false;
    status.isLoading.value = true;
    return axios
      .get<ApiOrder>(`api/orders/${orderId}`, {
        baseURL: API_BASIC_URL,
        params: { userAccessKey: state.userAccessKey },
      })
      .then(({ data }) => {
        commit(MutationTypes.UPDATE_ORDER_INFO, data);
      })
      .catch(() => {
        status.isLoadingFailed.value = true;
      })
      .then(() => {
        status.isLoading.value = false;
      });
  },

  [ActionTypes.LOAD_CART]({ commit, state }) {
    state.cartStatus.isLoading = true;
    state.cartStatus.isLoadingFailed = false;
    return axios
      .get<ApiGetCartProducts>("api/baskets", {
        baseURL: API_BASIC_URL,
        params: { userAccessKey: state.userAccessKey },
      })
      .then((response) => {
        if (!state.userAccessKey) {
          localStorage.setItem(
            Names.USER_ACCESS_KEY,
            response.data.user.accessKey
          );
          commit(
            MutationTypes.UPDATE_USER_ACCESS_KEY,
            response.data.user.accessKey
          );
        }
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, response.data.items);
      })
      .catch(() => {
        state.cartStatus.isLoadingFailed = true;
      })
      .then(() => {
        state.cartStatus.isLoading = false;
      });
  },

  [ActionTypes.ADD_PRODUCT_TO_CART]({ commit, state }, { payload, status }) {
    state.cartStatus.isLoading = true;
    state.cartStatus.isLoadingFailed = false;
    status.isLoading.value = true;
    status.isLoadingFailed.value = false;
    status.isCompleted.value = false;
    return axios
      .post<ApiPostCartProducts>("api/baskets/products", payload, {
        baseURL: API_BASIC_URL,
        params: { userAccessKey: state.userAccessKey },
      })
      .then(({ data }) => {
        status.isCompleted.value = true;
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, data.items);
      })
      .catch(() => {
        status.isLoadingFailed.value = true;
        state.cartStatus.isLoadingFailed = true;
      })
      .then(() => {
        status.isLoading.value = false;
        state.cartStatus.isLoading = false;
      });
  },
  [ActionTypes.UPDATE_PRODUCT_CART]({ commit, state }, { payload, status }) {
    status.isLoading.value = true;
    status.isLoadingFailed.value = false;
    return axios
      .put<ApiPutCartProducts>("api/baskets/products", payload, {
        baseURL: API_BASIC_URL,
        params: { userAccessKey: state.userAccessKey },
      })
      .then(({ data }) => {
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, data.items);
      })
      .catch(() => {
        status.isLoadingFailed.value = true;
      })
      .then(() => {
        status.isLoading.value = false;
      });
  },
  [ActionTypes.DELETE_PRODUCT_FROM_CART](
    { commit, state },
    { basketItemId, status }
  ) {
    status.isLoading.value = true;
    status.isLoadingFailed.value = false;
    return axios
      .delete<ApiDeleteCartProducts>("api/baskets/products", {
        baseURL: API_BASIC_URL,
        data: { basketItemId },
        params: { userAccessKey: state.userAccessKey },
      })
      .then(({ data }) => {
        commit(MutationTypes.UPDATE_CART_PRODUCTS_DATA, data.items);
      })
      .catch(() => {
        status.isLoadingFailed.value = true;
      })
      .then(() => {
        status.isLoading.value = false;
      });
  },
};

export const getters: GetterTree<State, State> & Getters = {
  numberOfPositions(state: State): number {
    return state.cartProductsData.length;
  },
  cartProductsData(state: State): Array<CartProductType> {
    return state.cartProductsData;
  },
  wholeCost(state: State): number {
    return state.cartProductsData.reduce(
      (acc, value) => acc + value.quantity * value.price,
      0
    );
  },
  cartStatus(state: State): CartStatus {
    return state.cartStatus;
  },
};

export const store = createStore({
  state,
  mutations,
  getters,
  actions,
  plugins: [createLogger()],
}) as CustomStore;

export const useStore = () => {
  return store;
};
