import { CheckboxType, CheckboxType2 } from "@/types/custom/checkbox-type";
import { reactive } from "vue";
import {
  ApiCategories,
  ApiColors,
  ApiMaterials,
  ApiSeasons,
} from "@/types/api-types";
import axios from "axios";
import { API_BASIC_URL } from "@/others/basic-url";
import { CategoryType } from "@/types/basic/category-type";
import { ApiProductBody } from "@/types/basic/api/api-product-body";
import useCreateRef from "@/composables/useCreateRef";
import useCreateReactiveArray from "@/composables/useCreateReactiveArray";

export default function (filter: ApiProductBody) {
  const toDo: Array<() => void> = [];
  const { element: minPrice } = useCreateRef<number>(filter.minPrice, 0, toDo);
  const { element: maxPrice } = useCreateRef<number>(filter.maxPrice, 0, toDo);
  const { element: categoryId } = useCreateRef<number>(
    filter.minPrice,
    0,
    toDo
  );
  const { element: materials } = useCreateReactiveArray<CheckboxType2>(
    [],
    false,
    toDo
  );
  const { element: seasons } = useCreateReactiveArray<CheckboxType2>(
    [],
    false,
    toDo
  );
  const { element: colors } = useCreateReactiveArray<CheckboxType>(
    [],
    false,
    toDo
  );
  const categories = reactive<Array<CategoryType>>([]);
  type newType<T> = T extends ApiMaterials | ApiSeasons
    ? CheckboxType2
    : CheckboxType;
  const loadForCheckboxes = <D extends ApiMaterials | ApiSeasons | ApiColors>(
    array: Array<newType<D>>,
    link: string,
    filtered: Array<number> = []
  ) => {
    axios.get<D>(link, { baseURL: API_BASIC_URL }).then((e) => {
      array.push(
        ...(e.data.items.map((e) => {
          return Object.assign(e, { checked: filtered.includes(e.id) });
        }) as Array<newType<D>>)
      );
    });
  };
  const loadCategories = () => {
    axios
      .get<ApiCategories>("api/productCategories", { baseURL: API_BASIC_URL })
      .then((e) => categories.push(...e.data.items));
  };
  loadCategories();
  loadForCheckboxes<ApiColors>(colors, "/api/colors", filter.colorIds);
  loadForCheckboxes<ApiSeasons>(seasons, "/api/seasons", filter.seasonIds);
  loadForCheckboxes<ApiMaterials>(
    materials,
    "/api/materials",
    filter.materialIds
  );
  return {
    toDo,
    minPrice,
    maxPrice,
    categoryId,
    seasons,
    colors,
    materials,
    categories,
  };
}
