
import { computed, defineComponent, PropType, ref } from "vue";
import { CartProductType } from "@/types/basic/cart-product-type";
import formatNumber from "@/helpers/formatNumber";
import BaseCounter from "@/components/Base/BaseCounter.vue";
import { useStore } from "@/store";
import { ActionTypes } from "@/types/store-type";
import useStatus from "@/composables/useStatus";

export default defineComponent({
  components: { BaseCounter },
  props: {
    product: { type: Object as PropType<CartProductType>, required: true },
  },
  setup(props) {
    const status = useStatus();
    const store = useStore();
    const deleteProduct = () => {
      store.dispatch(ActionTypes.DELETE_PRODUCT_FROM_CART, {
        status,
        basketItemId: props.product.id,
      });
    };
    const currCount = ref(props.product.quantity);
    const count = computed({
      get: () => currCount.value,
      set: (value: number) => {
        if (!value) return;
        store.dispatch(ActionTypes.UPDATE_PRODUCT_CART, {
          status,
          payload: {
            basketItemId: props.product.id,
            quantity: value,
          },
        });
        currCount.value = value;
      },
    });
    const prettyPrice = computed(() =>
      formatNumber(props.product.price * count.value)
    );
    return { prettyPrice, count, deleteProduct, status };
  },
  name: "CartItem",
});
