
import {
  computed,
  defineComponent,
  onBeforeMount,
  reactive,
  ref,
  watch,
} from "vue";
import { RouteLocationRaw, useRoute, useRouter } from "vue-router";
import { API_BASIC_URL } from "@/others/basic-url";
import useLoadAxios from "@/composables/useLoadAxios";
import { ApiItem } from "@/types/api-types";
import BaseStatus from "@/components/Base/BaseStatus.vue";
import { PrettyPrice } from "@/types/custom/pretty-price";
import formatNumber from "@/helpers/formatNumber";
import BaseCheckboxDecorator from "@/components/Base/Wrappers/BaseCheckboxDecorator.vue";
import BaseColors from "@/components/Base/BaseColors.vue";
import { ColorType } from "@/types/basic/color-type";
import ProductInfo from "@/components/Products/ProductInfo.vue";
import { useStore } from "@/store";
import { ActionTypes } from "@/types/store-type";
import { SizeType } from "@/types/basic/size-type";
import useCharacteristic from "@/composables/useCharacteristic";
import BaseCounter from "@/components/Base/BaseCounter.vue";
import { AddStatus } from "@/types/custom/status";

export default defineComponent({
  components: {
    BaseCounter,
    BaseStatus,
    BaseCheckboxDecorator,
    BaseColors,
    ProductInfo,
  },
  setup() {
    const store = useStore();
    const currentInfo = ref(0);
    const count = ref(1);
    const currPhoto = ref(0);
    const route = useRoute();
    const router = useRouter();
    const {
      loadStatus,
      doLoadData: loadItem,
      data: product,
    } = useLoadAxios<ApiItem & PrettyPrice>(
      () => `api/products/${route.params.id}`,
      () => ({
        baseURL: API_BASIC_URL,
      }),
      (value) =>
        Object.assign(value, { prettyPrice: formatNumber(value.price) })
    );
    const info = reactive({
      colorId: Number(route.query.colorId) || 0,
      sizeId: Number(route.query.sizeId) || 0,
    });
    const changeUrl = (object: RouteLocationRaw) => {
      router.push(object);
    };
    const { comData: color, currData: currColor } =
      useCharacteristic<ColorType>(
        () => info,
        (value: number) => ({ colorId: value }),
        changeUrl,
        () => product.value?.colors
      );

    const { currData: currSize, comData: size } = useCharacteristic<SizeType>(
      () => info,
      (value: number) => ({ sizeId: value }),
      changeUrl,
      () => product.value?.sizes
    );
    const doChangeColor = (value: number, currValue = product.value) => {
      if (currValue?.colors) {
        const current = Number(value || 0);
        currColor.value =
          currValue.colors.find((e) => e.id === current) || currValue.colors[0];
      }
    };

    const doChangeSize = (value: number, currValue = product.value) => {
      if (currValue?.sizes) {
        const current = Number(value || 0);
        currSize.value =
          currValue.sizes.find((e) => e.id === current) || currValue.sizes[0];
      }
    };
    const prettyPrice = computed(() => {
      if (product.value) {
        return formatNumber(product.value.price * count.value);
      }
      return 0;
    });

    watch(
      () => product.value,
      (currValue) => {
        doChangeColor(Number(route.query.colorId || 0), currValue);
        doChangeSize(Number(route.query.sizeId) || 0, currValue);
      }
    );
    watch(
      () => route.params.id,
      () => {
        loadItem();
      }
    );
    watch(
      () => route.query.colorId,
      (value) => {
        doChangeColor(Number(value));
      }
    );

    watch(
      () => route.query.sizeId,
      (value) => {
        doChangeSize(Number(value));
      }
    );

    const status: AddStatus = {
      isLoading: ref(false),
      isLoadingFailed: ref(false),
      isCompleted: ref(false),
    };

    const doSendToCart = () => {
      count.value = 1;
      if (product.value && currColor.value && currSize.value) {
        store.dispatch(ActionTypes.ADD_PRODUCT_TO_CART, {
          payload: {
            productId: product.value.id,
            colorId: currColor.value.color.id,
            sizeId: currSize.value.id,
            quantity: count.value || 1,
          },
          status,
        });
      }
    };
    onBeforeMount(() => loadItem());
    return {
      doSendToCart,
      product,
      prettyPrice,
      loadStatus,
      loadItem,
      color,
      size,
      currColor,
      count,
      currPhoto,
      currentInfo,
      currSize,
      status,
    };
  },
  name: "ProductPage",
});
