import type {
  ActionContext,
  CommitOptions,
  DispatchOptions,
  Store as VuexStore,
} from "vuex";
import { ProductType } from "@/types/basic/product-type";
import { CartProductType } from "@/types/basic/cart-product-type";
import { ApiPutProductCartBody } from "@/types/basic/api/api-put-product-cart-body";
import { AddStatus, BaseStatus, CartStatus } from "@/types/custom/status";
import { ApiOrder } from "@/types/api-types";

export interface State {
  cartProductsData: Array<CartProductType>;
  cartOrderInfo: null | ApiOrder;
  userAccessKey: null | string;
  cartStatus: CartStatus;
}

export enum MutationTypes {
  UPDATE_USER_ACCESS_KEY = "UPDATE_USER_ACCESS_KEY",
  UPDATE_CART_PRODUCTS_DATA = "UPDATE_CART_PRODUCTS_DATA",
  RESET_CART = "RESET_CART",
  UPDATE_ORDER_INFO = "UPDATE_ORDER_INFO",
}

export enum ActionTypes {
  LOAD_CART = "LOAD_CART",
  LOAD_ORDER_INFO = "LOAD_ORDER_INFO",
  ADD_PRODUCT_TO_CART = "ADD_PRODUCT_TO_CART",
  UPDATE_PRODUCT_CART = "UPDATE_PRODUCT_CART",
  DELETE_PRODUCT_FROM_CART = "DELETE_PRODUCT_FROM_CART",
}

export type Mutations<S = State> = {
  [MutationTypes.UPDATE_USER_ACCESS_KEY](state: S, accessKey: string): void;
  [MutationTypes.UPDATE_CART_PRODUCTS_DATA](
    state: S,
    items: Array<CartProductType>
  ): void;
  [MutationTypes.RESET_CART](state: S): void;
  [MutationTypes.UPDATE_ORDER_INFO](state: S, payload: ApiOrder): void;
};
export type Getters = {
  numberOfPositions(state: State): number;
  cartProductsData(state: State): Array<CartProductType>;
  wholeCost(state: State): number;
  cartStatus(state: State): CartStatus;
};

export type AugmentedActionContext = {
  commit<K extends keyof Mutations>(
    key: K,
    payload?: Parameters<Mutations[K]>[1]
  ): ReturnType<Mutations[K]>;
} & Omit<ActionContext<State, State>, "commit">;

export interface Actions {
  [ActionTypes.LOAD_ORDER_INFO](
    { commit, state }: AugmentedActionContext,
    payload: { status: BaseStatus; orderId: number }
  ): Promise<void>;

  [ActionTypes.LOAD_CART]({
    commit,
    state,
  }: AugmentedActionContext): Promise<void>;

  [ActionTypes.ADD_PRODUCT_TO_CART](
    { commit, state }: AugmentedActionContext,
    payload: { payload: ProductType; status: AddStatus }
  ): Promise<void>;

  [ActionTypes.UPDATE_PRODUCT_CART](
    { commit, state }: AugmentedActionContext,
    info: { payload: ApiPutProductCartBody; status: BaseStatus }
  ): Promise<void>;

  [ActionTypes.DELETE_PRODUCT_FROM_CART](
    { commit, state }: AugmentedActionContext,
    info: { basketItemId: number; status: BaseStatus }
  ): Promise<void>;
}

export type CustomStore = Omit<
  VuexStore<State>,
  "commit" | "getters" | "dispatch"
> & {
  commit<K extends keyof Mutations, P extends Parameters<Mutations[K]>[1]>(
    key: K,
    payload: P,
    options?: CommitOptions
  ): ReturnType<Mutations[K]>;
} & {
  getters: {
    [K in keyof Getters]: ReturnType<Getters[K]>;
  };
} & {
  dispatch<K extends keyof Actions>(
    key: K,
    payload?: Parameters<Actions[K]>[1],
    options?: DispatchOptions
  ): ReturnType<Actions[K]>;
};
