import { reactive } from "vue";

export default function <T extends { checked: boolean }>(
  array: Array<T>,
  toDef: boolean,
  arrayToDo: Array<() => void>
) {
  const element = reactive(array);
  const toDefault = () =>
    Object.assign(
      element,
      element.map((e) => Object.assign(e, { checked: toDef }))
    );
  arrayToDo.push(toDefault);
  return { element, toDefault };
}
