
import { computed, defineComponent, ref } from "vue";
import { useStore } from "@/store";
import { useRoute } from "vue-router";
import { ActionTypes } from "@/types/store-type";
import OrderDictionaryItem from "@/components/Order/OrderDictionaryItem.vue";
import OrderList from "@/components/Order/OrderList.vue";
import formatNumber from "@/helpers/formatNumber";

export default defineComponent({
  components: { OrderList, OrderDictionaryItem },
  setup() {
    const store = useStore();
    const route = useRoute();
    const order = computed(() => store.state.cartOrderInfo);
    const info = computed(() => {
      if (order.value) {
        return [
          { title: "Получатель", value: order.value.name, id: 1 },
          {
            title: "Адрес доставки",
            value: order.value.address,
            id: 2,
          },
          { title: "Телефон", value: order.value.phone, id: 3 },
          {
            title: "Email",
            value: order.value.email,
            id: 4,
          },
          { title: "Способ оплаты", value: order.value.paymentType, id: 5 },
        ];
      }
      return [];
    });
    const wholePrice = computed(() => {
      if (order.value) {
        return formatNumber(order.value.totalPrice);
      }
      return "";
    });
    const currId = Number(route.params.id);
    const isILoaded =
      store.state.cartOrderInfo && store.state.cartOrderInfo.id === currId;
    const status = { isLoading: ref(false), isLoadingFailed: ref(false) };
    if (!isILoaded) {
      store.dispatch(ActionTypes.LOAD_ORDER_INFO, { orderId: currId, status });
    }
    return { order, status, info, wholePrice };
  },
  name: "OrderPageInfo",
});
