import { computed, ref, UnwrapRef } from "vue";

export default function <T extends { id: number }>(
  startedObject: () => { colorId: number; sizeId: number },
  toUpdate: (value: number) => { colorId: number } | { sizeId: number },
  callback: (value: { query: { colorId: number; sizeId: number } }) => void,
  second: () => Array<T> | undefined
) {
  const currData = ref(null as null | T);
  const comData = computed({
    get: () => {
      if (currData.value) {
        return currData.value.id;
      } else {
        return 0;
      }
    },
    set: (value: number) => {
      const currObject = startedObject();
      Object.assign(currObject, toUpdate(value));
      const all = second();
      if (all) {
        const currItem = all.find((e) => e.id === value);
        if (currItem) {
          currData.value = currItem as UnwrapRef<T>;
          callback({ query: currObject });
        }
      }
    },
  });
  return { comData, currData };
}
