
import { computed, defineComponent, PropType } from "vue";
import { CartProductType } from "@/types/basic/cart-product-type";
import formatNumber from "@/helpers/formatNumber";

export default defineComponent({
  setup(props) {
    const cost = computed(() =>
      formatNumber(props.product.quantity * props.product.price)
    );
    return { cost };
  },
  props: {
    product: { type: Object as PropType<CartProductType>, required: true },
  },
  name: "OrderItem",
});
