
import { computed, defineComponent, PropType } from "vue";
import BaseCheckBox from "@/components/Base/BaseCheckBox.vue";
import BaseCheckBox2 from "@/components/Base/BaseCheckBox2.vue";
import BaseCheckboxDecorator from "@/components/Base/Wrappers/BaseCheckboxDecorator.vue";
import useLoadFilters from "@/composables/useLoadFilters";
import { ApiProductBody } from "@/types/basic/api/api-product-body";
import { useFilterClass } from "@/composables/useFilterClass";

export default defineComponent({
  props: {
    modelValue: { required: true, type: Object as PropType<ApiProductBody> },
  },
  setup(props) {
    const {
      colors,
      seasons,
      materials,
      categories,
      categoryId,
      minPrice,
      maxPrice,
      toDo,
    } = useLoadFilters(props.modelValue);
    const isFilterWasUsed = computed(() => {
      return !!(
        props.modelValue.colorIds.length ||
        props.modelValue.materialIds.length ||
        props.modelValue.seasonIds.length ||
        props.modelValue.categoryId ||
        props.modelValue.maxPrice ||
        props.modelValue.minPrice
      );
    });
    const doUpdateFilter = () => {
      Object.assign(props.modelValue, {
        categoryId: categoryId.value,
        limit: 12,
        minPrice: minPrice.value,
        maxPrice: maxPrice.value,
        page: 1,
        colorIds: colors.filter((e) => e.checked).map((e) => e.id),
        materialIds: materials.filter((e) => e.checked).map((e) => e.id),
        seasonIds: seasons.filter((e) => e.checked).map((e) => e.id),
      });
    };
    const doResetFilter = () => {
      toDo.forEach((func) => {
        func();
      });
      Object.assign(props.modelValue, useFilterClass());
    };
    return {
      colors,
      materials,
      seasons,
      categories,
      doResetFilter,
      doUpdateFilter,
      categoryId,
      minPrice,
      maxPrice,
      isFilterWasUsed,
    };
  },
  components: { BaseCheckBox, BaseCheckBox2, BaseCheckboxDecorator },
  name: "ProductFilter",
});
